@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap");

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  font-family: "Manrope", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

.home-page {
  background-color: rgb(17, 127, 167);
  position: absolute;
  top: 0%;
  bottom: 0%;
  right: 0%;
  left: 0%;
  z-index: -1;
  /* padding-top: 15%; */
}

.footer {
  position: absolute;
  bottom: 0%;
  right: 0%;
  left: 0%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgb(247, 232, 161);
  color: rgb(4, 44, 59);
  padding-left: 2%;
  padding-right: 2%;
  padding-top: 0.5%;
  padding-bottom: 0.5%;
}

.footer > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.portfolios {
  width: 13.5em;
}

.portfolios p {
  padding-right: 1%;
}

.portfolios > ul {
  border: 1px solid black;
  border-radius: 10%;
  padding: 2%;
}

span {
  font-weight: bold;
  opacity: 0.8;
}

span:hover {
  opacity: 1;
}

.pokeball-footer img {
  width: 17%;
  margin: auto;
  cursor: pointer;
  opacity: 0.7;
}

.pokeball-footer img:hover {
  opacity: 1;
}

@media screen and (max-width: 600px) {
  .footer > div:first-child {
    display: none;
  }
  .pokeball-footer img {
    margin-left: 0;
    width: 25%
  }
  .pokeball-footer img:active {
    opacity: 1;
  }
  .portfolios {
    padding-right: 3%;
}
  .portfolios p {
    text-align: center;
    padding-right: 5%;
  }
  .portfolios span, .portfolios p {
    font-size: 90%;
  }
}

.click-me-msg {
  position: absolute;
  bottom: 12%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: bolder;
  color: rgb(247,232,161);
}
.msg-p:after {
    content: " ";
    position: absolute;
    right: 49.5%;
    bottom: -15px;
    border-top: 15px solid rgb(15, 166, 221);
    border-right: 15px solid transparent;
    border-left: 15px solid transparent;
    border-bottom: none;
}

.click-me-msg p {
  border-radius: 5px;
  padding: 0.5%;
  background-color: rgb(15, 166, 221);
  font-weight: bolder;
}



