.pokemon-card {
  position: absolute;
  margin: auto;
  top: 42%;
  right: 13%;
  background-color: #f2f2f2;
  padding: 1% 0;
  border-radius: 15px;
  border: solid 1px #dbdbdb;
  width: 22%;
}

.pokemon-stats,
.pokemon-types,
.pokemon-btns {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}

.single-pokemon {
  text-align: center;
  margin-bottom: 3%;
}

.pokemon-card img {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: auto;
}

.pokemon-card span {
  color: grey;
}

.height-weight {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 4% 0 6% 0;
}

.pokemon-card hr {
  width: 75%;
  border: solid 1px #dbdbdb;
  margin: auto;
}

.pokemon-stats {
  margin-top: 5%;
}

.pokemon-btns {
  margin: 6% 0;
}

.pokemon-btns button {
  font-weight: bold;
  color: white;
  padding: 5%;
  border-radius: 25px;
  background: -webkit-linear-gradient(
    315deg,
    rgba(230, 250, 255, 1) 0%,
    rgba(68, 164, 199, 1) 40%,
    rgba(20, 144, 189, 1) 100%
  );
  background: -o-linear-gradient(
    315deg,
    rgba(230, 250, 255, 1) 0%,
    rgba(68, 164, 199, 1) 40%,
    rgba(20, 144, 189, 1) 100%
  );
  background: linear-gradient(
    135deg,
    rgba(230, 250, 255, 1) 0%,
    rgba(68, 164, 199, 1) 40%,
    rgba(20, 144, 189, 1) 100%
  );
  border: 1px solid #44a4c7;
  width: 100px;
  height: 30px;
  cursor: pointer;
}

.pokemon-btns button:hover {
  background: -webkit-linear-gradient(
    40deg,
    rgba(230, 250, 255, 1) 0%,
    rgba(68, 164, 199, 1) 40%,
    rgba(20, 144, 189, 1) 100%
  );
  background: -o-linear-gradient(
    40deg,
    rgba(230, 250, 255, 1) 0%,
    rgba(68, 164, 199, 1) 40%,
    rgba(20, 144, 189, 1) 100%
  );
  background: linear-gradient(
    50deg,
    rgba(230, 250, 255, 1) 0%,
    rgba(68, 164, 199, 1) 40%,
    rgba(20, 144, 189, 1) 100%
  );
}

.pokemon-btns button:focus {
  opacity: 0.7;
}

.pokemon-btns button:active {
  background: -webkit-linear-gradient(
    40deg,
    rgba(230, 250, 255, 1) 0%,
    rgb(243, 71, 71) 40%,
    rgb(189, 20, 20) 100%
  );
  background: -o-linear-gradient(
    40deg,
    rgba(230, 250, 255, 1) 0%,
    rgb(243, 71, 71) 40%,
    rgb(189, 20, 20) 100%
  );
  background: linear-gradient(
    50deg,
    rgba(230, 250, 255, 1) 0%,
    rgb(243, 71, 71) 40%,
    rgb(189, 20, 20) 100%
  );
  border: 1px solid rgb(189, 20, 20);
}

.pokemon-btns p {
 color: red;
}

.close-card {
  color: grey;
  text-align: right;
  margin-right: 5%;
}

@media screen and (max-width: 600px) {
  .pokemon-card {
    width: 47%;
    right: 1.5%;
    top: 63%;
  }
  .height-weight {
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding: 2%;
    font-size: 0.75rem;
  }

  .pokemon-btns button {
    width: 85px;
    height:27px;
  }
}

@media screen and (max-height: 750px) {
    .pokemon-card {
        top: 58.5%;
    }
  }



