.form-container{
  display: -ms-flexbox;
  display: flex;
 -ms-flex-pack: center;
     justify-content: center;
  max-width: 100%;
}

.form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 70%;
  margin-top: 15px;
}

.form.row {
  -ms-flex-direction: row;
      flex-direction: row;
}

.form .input {
  height: 20%;
  margin-bottom: 20px;
  border: 2px solid rgb(4, 44, 59);
  border-radius: .3rem;
  padding: 5px 0 5px 20px;
  width:33%;
  color: rgb(4, 44, 59);
}

.form .icon {
  margin-bottom: 20px;
}

.form .label {
  cursor: pointer;
  margin-bottom: 5px;
  font-size: 1.3rem;
  color: rgb(4, 44, 59);
}

.form .btn {
  height: 100%;
  width: 12%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background: rgb(4, 44, 59);
  color: whitesmoke;
  cursor: pointer;
  border: none;
  border-radius: .3rem;
  font-size: 1.2rem;
  padding: 6px;
}

.form .btn:hover {
  background: #eb804c;
  color: black;
  border: 1px solid;
}

input[type="file"] {
  display: none;
}

.avatar {
  height: 32px;
  max-width: 120px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background: rgb(4, 44, 59);
  color: whitesmoke;
  cursor: pointer;
  border: none;
  border-radius: .3rem;
  padding:5px 7px;
  
}

.form .avatar:hover {
  background: #eb804c;
  color: black;
  border: 1px solid;

}

@media screen and (max-width: 600px) {
  .form .input {
    width: 60%;
    
  }

  .form .btn {
    width:35%;
  }
}





